.messages{
    display: grid;
    grid-template-columns: 10fr 2fr;
}

.dialog{
    background-color: #ffffff;
    height: 100vh;
    border-right: 1px solid #EBEBEB;
}
.dialogItems{
    background-color: #ffffff;
}
.dialogHead{
    border-bottom: 1px solid #EBEBEB;
    min-height: 80px;
}
.dialogBody{
    padding: 50px;
}
.dialogSearch{
    border-bottom: 1px solid #EBEBEB;
    min-height: 80px;
}



.messageOut{
    margin-left: auto;
    background-color: #E3EFFF;
}
.buttonForm{
    
    /*Reset's every elements apperance*/
    background: none repeat scroll 0 0 transparent;
    border: medium none;
    border-spacing: 0;
    color: #fff;
    background-color: #3C5A99;
    font-family: 'PT Sans Narrow',sans-serif;
    font-size: 16px;
    font-weight: normal;
    line-height: 35px;
    list-style: none outside none;
    margin: 0;
    padding: 0;
    text-align: left;
    text-decoration: none;
    text-indent: 0;
    display: block;
    width: 80px;
    cursor: pointer;
    border-radius: 4px;
   
    text-align: center;
    margin-left: auto;
}
